import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudentsForAssociationMem = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
  groupId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/Group/member`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studentCode,
      studentFullName,
      groupId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createPoliticalDate = ({ studentId, politicalDate, groupId }) =>
  request({
    prefix,
    url: `/Group/member`,
    params: {},
    data: { studentId, politicalDate, groupId },
    method: 'POST',
  });

export const deletePoliticalDate = ({ studentId, groupId }) =>
  request({
    prefix,
    url: `/Group/${groupId}/member/${studentId}`,
    params: {},
    data: { studentId, groupId },
    method: 'DELETE',
  });

export const exportPoliticStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  groupId,
  studentCode,
  studentFullName,
}) =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/Student/ExportGroupStudents`,
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      groupId,
      studentCode,
      studentFullName,
    },
  });



