import { request } from 'utils';

const prefix = '/api/students';

export const getGroupsForSearchAssociationMem = ({
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Group',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



