import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, DatePicker } from 'antd';
import { SelectAjax } from 'components/common/index';
import { dateToString } from 'utils/formatUtil';
import { getGroupsForSearchAssociationMem } from 'services/groupsForSearchAssociationMem';
import { createPoliticalDate } from 'services/studentForAssociationMem';

@Form.create({ name: 'FormStuAssociationMem' })
class FormStuAssociationMem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      studentId: null,
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ studentId: match.params.id });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { studentId } = this.state;
    try {
      await createPoliticalDate({
        studentId,
        politicalDate: dateToString(values.politicalDate, 'YYYY/MM/DD'),
        groupId: values.groupId,
      });
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card>
        <Row>
          <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Ngy kt np" hasFeedback>
              {getFieldDecorator('politicalDate', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<DatePicker />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tn hi" hasFeedback>
              {getFieldDecorator('groupId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<SelectAjax service={getGroupsForSearchAssociationMem} />)}
            </Form.Item>
            <div className="btn-group">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </div>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStuAssociationMem {...props} />
  </Page>
));



